<template>
<div>
  <header>
    <div>
      <navmenu></navmenu>
    </div>
    <div class="img-wrap">
      <img src="img/bg4.jpg"/>
    </div>
    <transition name="el-zoom-in-center">
      <div class="text-wrap" v-show="textshow">
          <h1>Publish</h1>
      </div>
    </transition>
    <div class="pointer-wrap" @click="viewContent">
      <i class="el-icon-arrow-down"></i>
    </div>
  </header>
  <article class="content">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="文章标题">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form-item label="文章标签">
        <el-select v-model="form.tag" placeholder="请选择标签">
          <el-option label="JavaScript" value="JavaScript"></el-option>
          <el-option label="Python" value="Python"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="编辑时间">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="文章内容">
        <el-input type="textarea" v-model="form.content"></el-input>
      </el-form-item>
      </el-form-item>
        <el-form-item label="作者">
          <el-input v-model="form.author"></el-input>
        </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即提交</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </article>
</div>
</template>

<script>
import navmenu from '../components/navmenu.vue'
export default {
    data() {
      return {
        textshow:false,
        form: {
          title: '',
          tag: '',
          date1: '',
          date2: '',
          content: '',
          author:''
        }
      }
    },
    components:{
      navmenu
    },
    mounted:function() {
      setTimeout(() => {
        this.textshow = true
      }, 500);
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      viewContent() {
        var content = document.querySelector(".pointer-wrap")
        document.documentElement.scrollTop = content.offsetTop-100
      }
    }
}
</script>

<style>
header {
  max-height: 70vh;
}
.content {
    font-size: 1.8rem;
    padding: 30px 10px;
    margin: 0 auto;
    max-width: 85vw;
    min-height: 61vh;
}
.content p{
    margin: 1.2rem 0;
    font-family: FZShuTi;
}
.content a{
    color: #fa5555;
}
</style>
